.single {
    display: flex;
    width: 100%;
    .singleContainer {
      flex: 6;
  
      .top {
        padding: 20px;
        display: flex;
        gap: 20px;
  
        .left {
            border-radius: 15px;
            flex: 1;
            -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
            box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
            padding: 20px;
            position: relative;
  
            .editButton {
                position: absolute;
                top: 0;
                right: 0;
                padding: 7px;
                margin: 20px;
                font-size: 15px;
                color: #7451f8;
                background-color: #7551f818;
                cursor: pointer;
                border-radius: 5px;
            }
  
          .item {
            display: flex;
            gap: 20px;
  
            .avatar {
              width: 100px;
              height: 100px;
              border-radius: 50%;
              object-fit: cover;
            }
  
            .details {
              .itemTitle {
                margin-bottom: 20px;
                color: #555;
              }
  
              .itemDetail {
                display: flex;
                gap: 20px;
                align-items: center;
                margin-bottom: 10px;
                font-size: 14px;
  
                .itemKey {
                  display: flex;
                  font-weight: bold;
                  color: gray;
                  margin-right: 5px;
                  margin-bottom: 10px;
                }
  
                .itemValue {
                  font-weight: 300;
                  margin-bottom: 10px;
                  color: rgb(156, 147, 147);
                }
              }
            }
          }
        }
  
        .right {
            border-radius: 15px;
            flex: 2;
        }
      }
  
      .bottom {
        border-radius: 15px;
        -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);
        box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
        padding: 20px;
        margin: 10px 20px;
      }
  
      .title {
        font-size: 16px;
        color: gray;
        margin-bottom: 20px;
      }
    }
  }