.widget {
    display: flex;
    justify-content: space-between;
    flex: 1;
    padding: 10px;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, 0.47);// for box shadow
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);// go to website box shadow generator
    height: 100px;
    border-radius: 10px;

    .left,
    .right {
        display: flex;
        flex-direction: column; // to be vertical
        justify-content: space-between;
    }

    .title {
        font-weight: bold;
        font-size: 14px;
        color: rgb(187, 179, 179);
    }

    .counter {
        font-size: 25px;
        font-weight: 300;
    }

    .link {
        font-size: 12px;
        border-bottom: 1px solid gray;
        width: max-content;// to make the border same as word length
        cursor: pointer;
    }

    .percentage {
        display: flex;
        align-items: center;
        font-size: 15px;
        
        &.positive {
            color: green;
        }
        &.negative {
            color: red;
        }
    }

    .icon {
        font-size: 20px;
        padding: 5px;
        border-radius: 5px;
        align-self: flex-end; // to be at it
    }
}